/* Large Devices, Wide Screens */
@media only screen and (max-width : 1199px) {
  .wave {
    &__wrapper {
      padding-top: 100px; }
    &__input {
      width: 240px; }
    &__btn {
      width: 240px; }
    &__form {
      &-wrapper {
        padding-left: 63px;
        padding-right: 63px; } }
    &__content {
      &-img {
        width: 476px;
        img {
          &:first-child {
            width: 476px;
            bottom: 12px;
            &+img {
              width: 462px;
              left: -6px; } } } }
      &-block {
        width: 213px;
        &>div {
          line-height: 1.2;
          &:first-child {
            width: 132px; } } }
      &-row {
        &>div {
          &+div {
            margin-left: 30px; } } } } }
  .header {
    &__social {
      margin-right: 13px; }
    &__nav {
      padding-right: 295px; } } }

/* Medium Devices, Desktops */
@media only screen and (max-width : 991px) {
  .wave {
    &__wrapper {
      padding-top: 90px; }
    &__form {
      display: none; }
    &__title {
      width: 530px;
      margin-bottom: 65px;
      strong {
        margin-left: 68px; } }
    &__content {
      display: flex;
      flex-direction: column;
      &>div {
        order: 1;
        &:first-child {
          order: 2; } }
      &-body {
        width: 100%;
        max-width: 610px;
        display: block;
        margin: 0 auto; }
      &-img {
        position: relative;
        right: inherit;
        bottom: inherit;
        width: 100%;
        max-width: 462px;
        height: 310px;
        margin: 0 auto; }
      &-block {
        width: 290px;
        &>div {
          &:first-child {
            width: 205px; } }
        br {
          display: none; } }
      &-row {
        &>div {
          &+div {
            margin-left: 15px; } } } }
    &__bottom {
      display: block; } }
  .header {
    &__nav {
      &-btn {
        display: block; } } }
  .navigation {
    position: absolute;
    width: 260px;
    border-radius: 7px;
    background: $color-red;
    color: $color-white;
    text-align: left;
    box-shadow: 0 0 40px rgba($color-black,.4);
    margin-top: 10px;
    opacity: 0;
    z-index: -1;
    transform: translateY(10px);
    transition: transform 0.35s;
    // display: none
    &::before {
      position: absolute;
      content: '';
      background: url('data:image/png;base64,') 0 0 no-repeat;
      width: 23px;
      height: 16px;
      top: -7px;
      left: 44px; }
    &__ul {
      font-size: rem(20);
      font-family: $font-roboto;
      padding: 27px 0 21px 0;
      &>li {
        display: block;
        margin: 0;
        &>a,&>span {
          display: block;
          margin: 0;
          color: $color-white;
          height: 36px;
          line-height: 36px;
          padding: 0 28px; }
        &>a {
          &:hover {
            color: $color-white; } } } }
    &__social {
      display: block;
      background: #ff6651;
      color: #ab2310;
      font-family: $font-roboto-bold;
      font-size: rem(12);
      text-transform: uppercase;
      padding: 13px 0 13px 28px;
      height: 57px;
      margin-bottom: 1px;
      span {
        display: inline-block;
        width: 85px;
        vertical-align: middle;
        margin-right: 33px; }
      a {
        display: inline-block;
        vertical-align: middle;
        svg {
          path {
            fill: #ab2310; } }
        &+a {
          margin-left: 9px; } } }
    &__phone {
      display: block; }
    &.is-active {
      opacity: 1;
      z-index: 1050;
      transform: translateY(0); } } }

/* Small Devices, Tablets */
@media only screen and (max-width : 767px) {

  .animated {
    transition-property: none !important;
    transform: none !important;
    animation: none !important; }

  .wave {
    padding-top: 0;
    &__title {
      width: 450px;
      font-size: rem(25);
      strong {
        font-size: rem(45);
        margin-left: 37px; } }
    &__content {
      &-body {
        width: 452px; }
      &-block {
        width: 217px;
        br {
          display:block {} } }
      &-row {
        margin-bottom: 55px; }
      &-img {
        width: 435px;
        img {
          width: 435px !important; } } } }
  .content {
    &__head {
      margin-bottom: 45px; } }
  .header {
    margin-top: 27px;
    &__top {
      display: block; }
    &__phone {
      display: none; }
    &__nav {
      padding: 0;
      float: right;
      margin-right: -205px; }
    &__social {
      margin-right: 140px;
      margin-top: 10px; } }
  .navigation {
    right: 13px;
    &::before {
      left: 154px; } } }

/* Extra Small Devices, Phones */
@media only screen and (max-width : 479px) {
  .h1, h1 {
    font-size: rem(25); }
  .content {
    &__head {
      &-untitle {
        font-size: rem(16); } } }
  .wave {
    &__wrapper {
      padding-top: 60px; }
    &__content {
      &-img {
        width: 270px;
        height: 182px;
        img {
          width: 270px !important; } }
      &-body {
        width: 230px; }
      &-row {
        display: flex;
        flex-direction: column;
        margin-bottom: 25px;
        &>div {
          &+div {
            margin-left: 0;
            margin-top: 25px; } } }

      &-block {
        &>div {
          &:first-child {
            width: 145px; } }
        img {
          float: right; } } }
    &__title {
      width: auto;
      font-size: rem(20);
      margin-bottom: 40px;
      strong {
        font-size: rem(30);
        margin-left: 0;
        text-align: right; } }
    &__container {
      padding-bottom: 70px;
      padding-top: 0; } }
  .header {
    &__social {
      display: none; }
    &__nav {
      margin-right: 0;
      margin-left: 0;
      &-btn {
        width: 30px;
        span {
          display: none; }
        i {
          font-size: rem(19);
          margin: 0; } } } }
  .navigation {
    &::before {
      left: inherit;
      right: 5px; } } }

